<template>
  <div id="footer">
    <el-button @click="backPrint"></el-button>
    <el-button type="primary" @click="primary"></el-button>
    <el-button type="success"></el-button>
    <el-button type="info" @click="setShowIntro(true)"></el-button>
    <el-button type="warning" @click="logout"></el-button>
    <el-button type="danger" @click="repair"></el-button>
  </div>
</template>
<script>
import {mapActions,mapGetters} from 'vuex'
export default {
  name: "process-foot",
  computed:{
    ...mapGetters([
      "facilityState"
    ])
  },
  methods: {
    ...mapActions([
      "setFacilityState","setUserName","setShowIntro"
    ]),
    backPrint() {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        inputPattern:/^bar/,
        inputErrorMessage:"bar"
      })
        .then(({ value }) => {
          this.$message({
            type: "success",
            message:  value+""
          });
        })
        .catch(() => {
        });
    },
    repair(){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.setFacilityState("REPAIR")
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
         
        });
    },
    logout(){
      this.setUserName("")
    },
    primary() {
      this.$message("");
    }
  },
  created() {
    let fstate = localStorage.getItem("facilityState");
    console.log(fstate)
    if(fstate){
      this.setFacilityState(fstate)
      if(fstate=="REPAIR"){
        this.$message("")
      }
    }
  },
  watch:{
    facilityState(val){
      if(!val){
        this.$message("");
        this.setFacilityState("")
      }
    }
  }
};
</script>
<style scoped>
#footer {
  text-align: center;
}
</style>

