<template>
  <div>
    <el-dialog
      title="(510)"
      width="80%"
      :visible="showQC"
      :close-on-press-escape="false"
      :show-close="false"
    >
      <el-table :data="qcTest" border style="width: 100%">
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="ismanual" label="">
          <template slot-scope="scope">{{scope.row.ismanual?"":""}}</template>
        </el-table-column>
        <el-table-column prop="type" label="">
          <template slot-scope="scope">{{scope.row.type==1?"NG/OK":"0,10"}}</template>
        </el-table-column>
        <el-table-column prop="result" label="">
          <template slot-scope="scope">
            <div v-if="!scope.row.ismanual">{{scope.row.result}}</div>
            <div v-else-if="scope.row.type==1">
              <el-radio-group v-model="scope.row.result">
                <el-radio-button label="NG"></el-radio-button>
                <el-radio-button label="OK"></el-radio-button>
              </el-radio-group>
            </div>
            <div v-else>
              <el-input type="number" v-model.lazy="scope.row.result" @change="qcMatch"></el-input>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <div slot="footer">
        <el-button @click="cancel"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { Loading } from 'element-ui';
export default {
  name: "process_qctest",
  data() {
    return {
      show: false,
      input: "",
      changed: false,
      qcTest: [
        {
          name: "",
          ismanual: false,
          type: 1, //1NG/OK,2
          result: ""
        },
        {
          name: "",
          ismanual: false,
          type: 2,
          result: ""
        },
        {
          name: "",
          ismanual: true,
          type: 1,
          result: ""
        },
        {
          name: "",
          ismanual: true,
          type: 2,
          result: ""
        }
      ]
    };
  },
  computed: {},
  props: ["showQC"],
  methods: {
    cancel() {
      this.$confirm("")
        .then(_ => {
          this.$emit("cancel");
          done();
        })
        .catch(_ => {});
    },
    qcMatch() {
      //
      let allTest = this.qcTest.some(q => {
        return q.result == "";
      });
      if (!allTest) {
        let result = this.qcTest.every(q => {
          if (q.type == 1) {
            return q.result == "OK";
          } else {
            return q.result >= 0 && q.result <= 10;
          }
        });
        if (result) {
          this.$message({
            message: "",
            type: "success"
          });
          const loading  = Loading.service({
            lock: true,
            text: "",
            spinner: "el-icon-loading",
            background: "rgba(0, 0, 0, 0.3)"
          });
          setTimeout(() => {
            loading.close();
           this.$emit('update:showQC', false)
          }, 2000);
        } else {
          this.$message({
            message: ",",
            type: "error"
          });
           this.$emit('update:showQC', false)
        }
      }
    }
  },
  watch: {
    showQC(val) {
      if (val) {
        this.qcTest = [
          {
            name: "",
            ismanual: false,
            type: 1, //1NG/OK,2
            result: ""
          },
          {
            name: "",
            ismanual: false,
            type: 2,
            result: ""
          },
          {
            name: "",
            ismanual: true,
            type: 1,
            result: ""
          },
          {
            name: "",
            ismanual: true,
            type: 2,
            result: ""
          }
        ];
        setTimeout(() => {
          let rand = Math.random() > 0.3 ? "OK" : "NG";
          this.qcTest.splice(0, 1, {
            name: "",
            ismanual: false,
            type: 1, //1NG/OK,2
            result: rand
          });
        }, 5000);
        setTimeout(() => {
          let rand = parseInt(Math.random() * 15);
          this.qcTest.splice(1, 1, {
            name: "",
            ismanual: false,
            type: 2,
            result: rand
          });
        }, 10000);
      }
    },
    qcTest: {
      handler: function(newVal, oldVal) {
        console.log(newVal[3].result, oldVal[3].result);
        if (newVal[3].result == oldVal[3].result) {
          this.qcMatch();
        }
      },
      deep: true
    }

  }
};
</script>
